.App {
  position: relative;
  text-align: center;
  background-color: rgb(67, 126, 87);
  min-height: 100vh;
  color: rgb(31, 31, 31);
  padding: 10px;
}

.change_button {
  display: block;
  width: fit-content;
  margin-left: auto;
  background-color: rgb(243, 243, 243);
  border: none;
  padding: 10px;
  border-radius: 20px;
  transition: 0.2s;
  cursor: pointer;
}

.change_button:hover {
  background-color: #4096ff;
  color: rgb(243, 243, 243);
}